.footer {
    padding: 50px 15% 10px;
    border-top: 6px solid #1d1b1b;
    color: #958c8c;
}

.footer h2 {
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 30px;

}

.footer .col {
    flex-basis: 25%;
    flex-grow: 1;
    margin-bottom: 20px;
}

.footer .col a {
    display: block;
    color: #777;
    font-size: 14px;
    margin-bottom: 10px;

}

.footer .row {
    align-items: flex-start;
    padding: 10px 0;

}

.footer .language-btn {
    color: white;
    padding: 10px 20px;
    border-radius: 3px;
}

.copyright-text {
    font-size: 14px;
    margin-top: 20px;
    margin-bottom: 10px;
}
